<template>
  <div class="__Home_Container">
    <div class="__Container _L">
      <!-- 圆点背景图 -->
      <div>
        <img class="__Dot_BgImg _1" src="https://pic.imgdb.cn/item/6397ede1b1fccdcd36aebd2b.png">
        <img class="__Dot_BgImg _2" src="https://pic.imgdb.cn/item/6397ede1b1fccdcd36aebd2b.png">
      </div>

      <div class="__Slash_Container">
        <div 
          v-for="(e, i) in 3"
          :key="i"
          class="__Items"
          :style="{ 'background-color' : `#${_MaskingBgColor}` }"
        ></div>
      </div>

      <div class="__Message_Container">
        <p class="__T _1">DESIGNER</p>
        <p class="__T _2">CIYE</p>
        <div class="__Line"></div>
        <img class="__NZ" src="https://pic.imgdb.cn/item/639140e1b1fccdcd365139e2.png" alt="不要点我!!">
        <!-- https://pic.imgdb.cn/item/639138fbb1fccdcd364aab09.png  黑色字体 -->
        <!-- https://pic.imgdb.cn/item/639136e6b1fccdcd3648e754.png  白色字体 -->
        <img class="__Name" src="https://pic.imgdb.cn/item/639138fbb1fccdcd364aab09.png" alt="斋藤飞鸟个性名字">
      </div>

      <div class="__Title_Container">
        <div class="__T_Box _1">
          <p 
            class="__T" 
            :style="{ 'color': `#${_TitleColor}` }"
          >
            {{ _Title_1 }}
          </p>
          <div 
            class="__Masking" 
            :style="{ 'background-color' : `#${_MaskingBgColor}` }"
          ></div>
        </div>
        <div class="__T_Box _2">
          <p 
            class="__T" 
            :style="{ 'color': `#${_TitleColor}` }"
          >
          {{ _Title_2 }}
          </p>
          <div 
            class="__Masking" 
            :style="{ 'background-color' : `#${_MaskingBgColor}` }"
          ></div>
        </div>
      </div>

      <div class="__ProgressBar __W_Add_Anima"></div>
    </div>

    <div 
      class="__Container _R"
      @mouseenter="R_MouseEnter"
      @mouseleave="R_MouseLeave"
    >
      <div class="__Img_Container">
        <img 
          class="__Img"
          :src="_Img_1"
        >
        <div class="__Switch_Img_Box">
          <div
            v-for="(e, i) in 4"
            :key="i"
            class="__Switch_Img"
          >
            <img class="__Imgs" :src="_Img_2">
          </div>
        </div>
      </div>

      <div class="__Overall_Progress_Container">
        <div class="__Overall_Progress_Box">
          <div class="__Overall_Progress_Bg">
            <div 
              class="__Overall_Progress_Line"
              :style="{ 'width' : `${_OverallProgressLine_W}px` }"
            ></div>
          </div>
        </div>

        <div class="__Arrows_Box">
          <p 
            class="iconfont icon-arrow-left"
            @click="PreOrNextFn(false)"
          ></p>
          <p 
            class="iconfont icon-arrow-right"
            @click="PreOrNextFn(true)"
          ></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import { onMounted, reactive, ref } from 'vue';

  const _TextDataObj = reactive([
    { id: 1, lt: '丧气波', rt: '蒸一下', color: '333333', imgUrl: 'https://pic.imgdb.cn/item/638f4a8fb1fccdcd36b67c9f.jpg', alt: '齋藤飛鳥_丧汽波' },
    { id: 2, lt: '超光', rt: '二十有你', color: '3a8cf0', imgUrl: 'https://pic.imgdb.cn/item/638f48fcb1fccdcd36b28d25.jpg', alt: '齋藤飛鳥_超光' },
    { id: 3, lt: '光杵', rt: '终成就', color: '884aed', imgUrl: 'https://pic.imgdb.cn/item/638f4a8fb1fccdcd36b67c90.jpg', alt: '齋藤飛鳥_光杵' },
    { id: 4, lt: '初盛', rt: '启程开始', color: 'fb832d', imgUrl: 'https://pic.imgdb.cn/item/638f4a8fb1fccdcd36b67c6a.jpg', alt: '齋藤飛鳥_初盛' },
    { id: 5, lt: '平行', rt: '焕然一新', color: 'dfd0d5', imgUrl: 'https://pic.imgdb.cn/item/639073bab1fccdcd365a725c.jpg', alt: '齋藤飛鳥_平行' },
    { id: 6, lt: '耀荧', rt: '定要赢', color: '002e4f', imgUrl: 'https://pic.imgdb.cn/item/639073bab1fccdcd365a7272.jpg', alt: '齋藤飛鳥_耀荧' },
    { id: 7, lt: '传奇', rt: '致敬青春', color: '0068ea', imgUrl: 'https://pic.imgdb.cn/item/638f4a8fb1fccdcd36b67c7b.jpg', alt: '齋藤飛鳥_传奇' },
    { id: 8, lt: '之空', rt: '不断前行', color: '8eaec0', imgUrl: 'https://pic.imgdb.cn/item/639073bab1fccdcd365a728d.jpg', alt: '齋藤飛鳥_之空' },
    { id: 9, lt: '任素汐', rt: '嗨', color: '5ce772', imgUrl: 'https://pic.imgdb.cn/item/639ee805b1fccdcd36821ef2.jpg', alt: '任素汐_嗨' },
    { id: 10, lt: '易烊千玺', rt: '自由', color: '19191f', imgUrl: 'https://pic.imgdb.cn/item/639ee872b1fccdcd3682bbcb.jpg', alt: '易烊千玺_自由' },
    { id: 11, lt: '周杰伦', rt: '回忆', color: '0966cd', imgUrl: 'https://pic.imgdb.cn/item/639ee8c2b1fccdcd368329f4.jpg', alt: '周杰伦_回忆' },
  ])

  /**
   * 左侧属性值
   */
  const _Count = ref(0);
  const _TitleColor = ref(_TextDataObj[_Count.value].color);
  const _MaskingBgColor = ref(_TextDataObj[_Count.value].color);
  const _Title_1 = ref(_TextDataObj[_Count.value].lt);
  const _Title_2 = ref(_TextDataObj[_Count.value].rt);

  /**
   * 右侧属性值
   */
  const _Next_Img = ref(_Count.value);
  const _Img_1 = ref(_TextDataObj[_Count.value].imgUrl);
  const _Img_2 = ref(_TextDataObj[_Next_Img.value].imgUrl);
  const _Add_Size = 200 / _TextDataObj.length;
  const _OverallProgressLine_W = ref(_Add_Size);

  /**
   * 初始化定时器方法
   */
  let _ProgressBarFn, _SwitchFn;

  /**
   * 文本标题遮罩层 / 轮播图切换层 / 左侧进度条
   */
  let _Maskings, _SwitchImgs, _ProgressBar;

  /**
   * 是否禁用，做防抖处理
   */
  let _IsDisable = false;
  onMounted(() => {
    _IsDisable = true;
    _Maskings = document.querySelectorAll('.__Title_Container .__Masking');
    _SwitchImgs = document.querySelectorAll('.__Img_Container .__Switch_Img');
    _ProgressBar = document.querySelector('.__Container._L .__ProgressBar');
    R_MouseLeave();
  })

  /**
   * 点击上一个或下一个事件
   * @param {判断上一个或下一个的点击} bool 
   */
  const PreOrNextFn = (bool) => {
    if ( _IsDisable === false ) return;
    _IsDisable = false;
    PreOrNextSwitch(bool);
    SwitchFn(true);
  }

  /**
   * 切换上一个或下一个事件
   * @param {判断是点击了上一个还是下一个事件} bool 
   */
  const PreOrNextSwitch = (bool) => {
    if ( bool ) {
      _Count.value++;
      _Next_Img.value++;
      _OverallProgressLine_W.value += _Add_Size;

      if ( _Count.value >= _TextDataObj.length ) _Count.value = 0;
      if ( _Next_Img.value >= _TextDataObj.length ) _Next_Img.value = 0;
      if ( _OverallProgressLine_W.value > (_Add_Size * _TextDataObj.length) ) _OverallProgressLine_W.value = _Add_Size;
    } else {
      _Count.value--;
      _Next_Img.value--;
      _OverallProgressLine_W.value -= _Add_Size;

      if ( _Count.value < 0 ) _Count.value = (_TextDataObj.length - 1);
      if ( _Next_Img.value < 0 ) _Next_Img.value = (_TextDataObj.length - 1);
      if ( _OverallProgressLine_W.value < _Add_Size ) _OverallProgressLine_W.value = 200;
    }
  }

  /**
   * 鼠标进入右侧内容后
   */
  const R_MouseEnter = () => {
    _ProgressBar.classList.remove('__W_Add_Anima');
    clearInterval(_ProgressBarFn);
    clearInterval(_SwitchFn);
  }

  /**
   * 鼠标离开右侧内容后
   */
  const R_MouseLeave = () => {
    _ProgressBar.classList.add('__W_Add_Anima');
    _SwitchFnSetTime();
  }

  /**
   * 切换定时器事件
   */
  const _SwitchFnSetTime = () => {
    _SwitchFn = setInterval(() => {
      PreOrNextSwitch(true);
      SwitchFn();
    }, 6000);
  }

  /**
   * 切换轮播总事件
   * @param {判断是否为点击切换事件} isClick 
   */
  const SwitchFn = (isClick = false) => {
    /**
     * 循环设置属性
     */
    _Maskings.forEach( item => item.classList.add('__Move_Anima') );
    _SwitchImgs.forEach( item => item.classList.add('__Move_Anima') );
    _MaskingBgColor.value = _TextDataObj[_Count.value].color;
    _Img_2.value = _TextDataObj[_Next_Img.value].imgUrl;
    
    /**
     * 延后设置新的内容
     */
    setTimeout(() => {
      _TitleColor.value = _TextDataObj[_Count.value].color;
      _Title_1.value = _TextDataObj[_Count.value].lt;
      _Title_2.value = _TextDataObj[_Count.value].rt;
    }, 1000);

    /**
     * 延后设置新的图片
     */
    setTimeout(() => {
      _Img_1.value = _TextDataObj[_Count.value].imgUrl;
    }, 1760);

    /**
     * 结束初始化
     */
    setTimeout(() => {
      _Maskings.forEach( item => item.classList.remove('__Move_Anima') );
      _SwitchImgs.forEach( item => item.classList.remove('__Move_Anima') );
      if ( isClick ) _IsDisable = true;
    }, 2400);
  }

</script>

<style lang="less" scoped>
@import url('../assets/Font_Icons/iconfont.css');

  .__Home_Container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1080px;
    height: 450px;
    background: rgba(255, 255, 255, 1);

    .__Container {
      float: left;
      position: relative;
      height: 100%;
      overflow: hidden;

      &._L {
        width: 280px;

        .__Dot_BgImg {
          position: absolute;
          width: 130px;
          opacity: 0.76;

          &._1 {
            bottom: 40px;
          }

          &._2 {
            right: -16px;
          }
        }

        .__Slash_Container {
          position: absolute;
          left: 10px;
          top: 16px;
          display: flex;
          align-content: space-between;
          justify-content: center;
          flex-wrap: wrap;
          width: 16px;
          height: 26px;
          .__Items {
            width: 100%;
            height: 3px;
            transform: rotate(30deg);
            transition: all 300ms ease-in-out;
          }
        }

        .__Message_Container {
          position: absolute;
          top: 120px;
          padding: 0 10px;
          width: 130px;
          height: 200px;

          .__T {
            font-size: 14px;
            &._1 { 
              line-height: 0.5;
            }
          }

          .__Line {
            margin-top: 4px;
            width: 20px;
            height: 3px;
            background: #333;
          }

          .__NZ {
            margin-top: 20px;
            width: 36px;
          }

          .__Name {
            position: absolute;
            left: 10px;
            bottom: 0;
            width: 90px;
          }
        }

        .__Title_Container {
          position: absolute;
          top: 100px;
          right: 18px;
          width: 96px;
          height: 184px;

          .__T_Box {
            position: relative;
            float: left;
            width: 48px;
            overflow: hidden;

            .__T {
              font-size: 48px;
              font-weight: 900;
              line-height: 1;
            }

            .__Masking {
              position: absolute;
              top: -200%;
              width: 100%;
              height: 200%;
              transition: all 1600ms ease-in-out;

              &.__Move_Anima {
                animation: Masking_Move 2000ms cubic-bezier(0.86, 0.19, 0.13, 0.87) 1;
              }

              @keyframes Masking_Move {
                10% { top: -200%; opacity: 1; }
                90% { top: 200%; opacity: 1; }
                91% { top: 200%; opacity: 0;}
                100% { top: -200%; opacity: 0;}
              }
            }
          }
        }

        .__ProgressBar {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 0;
          height: 2PX;
          background: #333333;

          &.__W_Add_Anima {
            animation: ProgressBar_W_Add 6000ms linear infinite;
          }

          @keyframes ProgressBar_W_Add {
            0% { width: 0; }
            100% { width: 100%; }
          }
        }
      }

      &._R {
        width: 800px;

        .__Img_Container {
          width: 100%;
          height: 100%;

          .__Img {
            width: 100%;
          }

          .__Switch_Img_Box {
              position: absolute;
              top: 0;
              width: 100%;
              height: 100%;
            .__Switch_Img {
              position: relative;
              right: -1000px;
              width: 100%;
              height: 25%;
              overflow: hidden;
              &.__Move_Anima {
                animation: Imgs_Move 4400ms ease-in-out;
              }
              .__Imgs { 
                position: absolute;
                width: 100%; 
              }

              &:nth-of-type(1) {
                .__Imgs { top: 0; }
              }

              &:nth-of-type(2) {
                animation-delay: 100ms;
                .__Imgs { top: -112.5px; }
              }

              &:nth-of-type(3) {
                animation-delay: 200ms;
                .__Imgs { top: -225px; }
              }

              &:nth-of-type(4) {
                animation-delay: 300ms;
                .__Imgs { top: -337.5px; }
              }

              @keyframes Imgs_Move {
                0% { right: -1000px; opacity: 1; }
                28% { right: 0px; opacity: 1; }
                90% { right: 0px; opacity: 1; }
                91% { right: 0px; opacity: 0; }
                96% { right: 0px; opacity: 0; }
                97% { right: -1000px; opacity: 0; }
                100% { right: -1000px; opacity: 0; }
              }
            }
          }
        }

        .__Overall_Progress_Container {
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translate(-50%);
          padding: 0 10px;
          width: 280px;
          height: 28px;
          background-color: rgba(0, 0, 0, 0.66);

          .__Overall_Progress_Box {
            float: left;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 220px;
            height: 100%;

            .__Overall_Progress_Bg {
              position: relative;
              width: 200px;
              height: 2px;
              background: #707070;
              overflow: hidden;

              .__Overall_Progress_Line {
                position: absolute;
                left: 0;
                height: 2px;
                background: #FFFFFF;
                transition: all 300ms ease-in-out;
              }
            }
          }

          .__Arrows_Box {
            float: right;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            width: 60px;
            height: 100%;

            .icon-arrow-left, 
            .icon-arrow-right {
              padding: 10px;
              font-size: 10px;
              color: #FFFFFF;
              transform: scale(0.8);
              transition: color 300ms ease-in-out;

              &:hover { 
                color: #000000; 
                cursor: url('../assets/index_手掌.ico'), auto;
              }
            }
          }
        }
      }
    }
  }
</style>