<template>
  <div class="__Decorate_Container">
    <div class="__Top_Header_Box">
      <div class="__LR_Box _L">
        <div class="__Circular">
          <i class="__Day_T">{{ _Day }}</i>
        </div>
      </div>
      <div class="__Logo_Box">
        <img width="34" src="https://pic.imgdb.cn/item/63a941ed08b683016392b470.png">
      </div>
      <div class="__LR_Box _R">
        <div class="__Circular"></div>
      </div>
    </div>

    <i class="__MT">/WALLPAPER|COLLECTION|WORKS/&nbsp;/DESIGNER|CIYE/</i>

    <div class="__StarBox __L">
      <Star  
        class="__Stars" 
        v-for="(item, index) in 16"
        :key="index"
      >
      </Star>
    </div>

    <div class="__StarBox __R">
      <Star  
        class="__Stars" 
        v-for="(item, index) in 16"
        :key="index"
      >
      </Star>
    </div>
  </div>
</template>

<script setup>
  import { ref } from 'vue';
  import Star from './icons/Star.vue';

  let _Time = new Date();
  const _Day = ref(_Time.getDate());
</script>

<style lang="less" scoped>
@import url('../public/font.less');

  .__Decorate_Container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;

    .__Top_Header_Box {
      z-index: 9999;
      position: absolute;
      top: 36px;
      left: 50%;
      transform: translate(-50%);
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 0px;
      height: 40px;
      background: #FFFFFF;
      border-radius: 36px;
      overflow: hidden;
      animation: __Header_Box 1200ms ease-in-out 1 400ms forwards;

      @keyframes __Header_Box {
        0% { width: 0px; }
        100% { width: 210px; }
      }

      .__Logo_Box {
        padding-top: 2px;
      }

      .__LR_Box {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;

        &._L {
          .__Circular {
            width: 27px;
            height: 18px;
            border-radius: 10px;
            background-color: #000000;

            .__Day_T {
              display: inline-block;
              width: 100%;
              color: #FFFFFF;
              font-weight: 100;
              text-align: center;
            }
          }
        }
        
        &._R {
          .__Circular {
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background-color: #3a8cf0;
            box-shadow: 0 0 10px 0 #3a8cf0;
          }
        }
      }
    }

    .__MT {
      position: absolute;
      bottom: 36px;
      left: 50%;
      transform: translate(-50%) scale(0.76);
      font-size: 12px;
      color: #FFFFFFa8;
      font-weight: 100;
      letter-spacing: 32px;
    }

    .__StarBox {
      position: absolute;
      top: 0;
      padding: 40px 14px;
      display: flex;
      justify-content: center;
      align-content: space-between;
      flex-wrap: wrap;
      width: 12px;
      height: 100%;

      &.__L { left: 0; }
      &.__R { right: 0; }
      
      .__Stars {
        width: 18px;
        animation-name: __Stars;
        animation-duration: 2000ms;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;

        @keyframes __Stars {
          0% { opacity: 1; }
          50% { opacity: 0; }
          100% { opacity: 1; }
        }
      }
    }
  }

  @MAX: 16;
  .ArrowsAnimation(@i, @t) when (@i <= @MAX) {
    .__StarBox .__Stars:nth-of-type(@{i}) { animation-delay: ~'@{t}ms'; }
    .ArrowsAnimation(@i + 1, @t + 400)
  }

  .ArrowsAnimation(1, 1000);
</style>
